<template>
	<div class="container">
		<div class="title">
			<h1 class="name">{{personal.name}}</h1>
			<div class="info">
				<div class="context">
					<div class="base_info">
						<div v-for="item in personal.base_information" :key="item.id" :class="['content', 'iconfont','icon-'+item.icon]">
							{{ item.content }}
						</div>
					</div>
					<div class="contact">
						<div v-for="item in personal.contact_way" :key="item.id"
						     :class="['content', 'iconfont','icon-'+item.icon]">
							{{ item.content }}
						</div>
					</div>
				</div>
				<div class="QRcode"> <img src="../assets/qq.png">QQ</div>
				<div class="QRcode"> <img src="../assets/wechat.png">微信</div>
			</div>
		</div>

		<h3 class="classes_title">教育背景</h3>
		<div class="education">
			<div v-for="item in education_background" :key="item.id" class="context">
				<div v-if="item.time" class="time">{{item.time}}</div>
				<div v-if="item.degree" class="degree">{{item.degree}}</div>
				<div v-if="item.school" class="school">{{item.school}}</div>
				<div v-if="item.college" class="college">{{item.college}}</div>
				<div v-if="item.major" class="major">{{item.major}}</div>
			</div>
		</div>

		<h3 class="classes_title">项目经验</h3>
		<div class="project">
			<div v-for="item in project_experience" :key="item.id" class="context">
				<div class="head">
					<h4 class="title">{{item.title}}</h4>
					<div v-if="item.tag" class="tag">{{item.tag}}</div>
					<a v-if="item.link" class="link" :href="item.link">{{item.link}}</a>
					<my_switch :v-model="true"></my_switch>
				</div>
				<div v-if="item.brief" class="brief">{{item.brief}}</div>
				<div v-if="item.detail" class="detail">{{item.detail}}</div>
			</div>
		</div>

		<h3 class="classes_title">工作经历</h3>
		<div class="work">
			<div v-for="item in work_experience" :key="item.id" class="context">
				<div class="head">
					<h4 v-if="item.job_title" class="title">{{item.job_title}}</h4>
					<div v-if="item.time" class="time">{{item.time}}</div>
					<div v-if="item.company" class="company">{{item.company}}</div>
				</div>
				<div v-if="item.detail" class="detail">{{item.detail}}</div>
			</div>
		</div>

</template>

<script>
	import my_switch from "./switch";

	export default {
		name: "resume",
		components: {
			my_switch
		},
		data() {
			return {
				personal: {
					name: "翁鸿铨",
					base_information: [{
							title: "年龄",
							icon: "man",
							content: "25岁"
						},
						{
							title: "出生年月",
							icon: "calendar",
							content: "1994-11"
						},
						{
							title: "籍贯",
							icon: "home",
							content: "福建晋江"
						},
					],
					contact_way: [{
							title: "电话",
							icon: "phone",
							content: "+86 189-5993-9718"
						},
						{
							title: "邮箱",
							icon: "email",
							content: "chainhq@hotmail.com"
						},
						{
							title: "QQ",
							icon: "qq",
							content: "563394793"
						},
						{
							title: "微信",
							icon: "wechat",
							content: "yu563394793"
						},
					],
				},
				education_background: [{
					time: "目前(拟录取)",
					degree: "硕士在读",
					school: "中国科学技术大学",
					college: "先进技术研究院",
					major: "电子信息（软件工程方向）",
				}, {
					time: "2012.09 - 2016.06",
					degree: "本科",
					school: "福建农林大学",
					college: "机电工程学院",
					major: "机械设计制造及其自动化",
				}, ],
				project_experience: [{
						title: "密码存储工具(web)",
						tag: "Django后端+Uni-App前端",
						link: "",
						brief: "",
						detail: "研究生初试后我自学了Python和Vue，做了一个移动端的网页。用户记录账户信息，用户可自由设置登录方式、条目标签和条目图片。主界面模拟手机UI的缩略图形式。总体代码量为前端5000，后端1000，目前可以在我系统的虚拟机中工作。",
					},
					{
						title: "Excel表单处理",
						tag: "工作期间做的一个VBA脚本",
						link: "",
						brief: "",
						detail: "实现SolidWorks导出的BOM表的处理、单零件耗材整理、相关工作电子表格的生成以及零部件图纸的归档存储，节省每天一到两个小时的处理Excel文档的时间。",
					},
					{
						title: "自走棋种族职业表",
						tag: "",
						link: "",
						brief: "",
						detail: "19年自走棋游戏火爆的时候制作了一个生产种族职业交叉图表的小工具，版本更新后可以在几分钟内制作出最新版本的图表。",
					},
					{
						title: "网页样式插件",
						tag: "",
						link: "https://userstyles.org/styles/178855/douyu-easy",
						brief: "",
						detail: "在stylish上传了一个网页样式修改插件，修改的是douyu.com，去除页面内的广告和竞猜付费元素，提供给使用者一个纯粹的直播界面，一月份上传至今累计下载量200+。",
					}
				],
				work_experience: [{
						time: "2017.09 - 2018.09",
						company: "深圳市鑫信腾科技有限公司",
						job_title: "机械设计工程师",
						detail: "智能手机自动测试设备及夹治具的结构设计。作为音频测试设备的主要设计负责人。工作期间销售额超过5000万。两次推动并完成音频测试夹治具的标准化及模块化工作。将单机型开发周期从2个工作日降低至0.5个工作日；将新机型夹治具设计出错从80%降低至20%以下。",
					},
					{
						time: "2016.07 - 2017.08",
						company: "通达（厦门）科技有限公司",
						job_title: "产品开发工程师",
						detail: "苹果手机IP68级防水卡托橡胶注射成型的开发。期间有连续半年的两班倒（9点-10点半）经历。",
					}
				]
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.container {
		width: 80%;
		margin: 0 auto;
		background: #fff;
		border: 1px solid #a5d4fc;
		padding: 40px;
	}

	.container>.title {
		display: flex;
		height: 100px;
	}

	.title .name {
		line-height: 80px;
		width: 200px;
		margin: 0;
	}

	.title .info {
		/* width: calc(100% - 200px); */
		display: flex;
	}

	.title .QRcode {
		font-size: 16px;
		text-align: center;
		line-height: 20px;
		width: 100px;
	}

	.title .QRcode>img {
		width: 80px;
		height: 80px;
		margin: 0 10px;
	}

	.title .context {
		display: flex;
		flex-direction: column;
	}

	.title .base_info,
	.title .contact {
		display: flex;
	}

	.title .content {
		line-height: 40px;
		height: 40px;
		font-size: 20px;
		width: 270px;
	}

	.title .content::before {
		padding-right: 10px;
	}

	.classes_title {
		color: #558df5;
		border-bottom: 2px solid #a5d4fc;
		margin: 20px 0 0 0;
	}

	.education .context {
		margin: 10px 0 0 40px;
		display: flex;
	}

	.education .context div {
		line-height: 40px;
		height: 40px;
		width: 200px;
	}

	.education .context .time {
		width: 240px;
	}

	.education .context .degree {
		width: 120px;
	}

	.education .context .major {
		width: 260px;
	}

	.project .context,
	.work .context {
		margin-left: 40px;
	}

	.project .head,
	.work .head {
		height: 60px;
		line-height: 60px;
		display: flex;
		margin-top: 10px;
	}

	.project .title,
	.work .title {
		margin: 0;
		width: 300px;
	}

	.project .detail,
	.work .detail {
		text-indent: 2em;
		line-height: 40px;
	}

	.project .tag {
		width: 280px;
	}

	.work .time {
		width: 240px;
	}
</style>



